    font-weight: normal;
}

.title {
    padding-top: 20px;
}

.z-index {
    z-index: 6;
}

body {
    background: #333;
}
    .Table {
        height: 350px;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        direction: rtl;
        border-spacing: 0;
    }
    
    .Table>tbody {
        background: #63B4E0;
    }
    
    #up,
    #down {
        border: 1px solid;
    }
    
    .Red {
        width: 80%;
        background: red;
        height: 12px;
        direction: rtl;
        position: absolute;
    }
    
    #upper {
        top: 43.5%;
        z-index: 5;
    }
    
    .CurrentLevel {
        width: 100%;
        background: #63B4E0;
        position: absolute;
        bottom: 0;
    }
    
    .wrapper {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 50%;
        z-index: 1;
        top: 50%;
        /* border-radius: 5px; */
        transform: translate(-50%, -50%);
        background-image: linear-gradient(to top, #accbee 0%, #e7f0fd 100%);
        overflow: hidden;
    }
    
    .wave {
        width: 1000px;
        height: 972px;
        position: absolute;
        top: -25%;
        left: 50%;
        margin-left: -500px;
        margin-top: -500px;
        border-radius: 35%;
        background: rgba(255, 255, 255, .75);
        -webkit-animation: wave 15s infinite linear;
                animation: wave 15s infinite linear;
    }
    
    @-webkit-keyframes wave {
        from {
            transform: rotate(0deg);
        }
        from {
            transform: rotate(360deg);
        }
    }
    
    @keyframes wave {
        from {
            transform: rotate(0deg);
        }
        from {
            transform: rotate(360deg);
        }
    }
    
    #lower {
        top: 86%;
    }
    
    .Table>tbody>tr {
        height: -1px;
    }
    
    .White {
        background: white !important;
    }
#right,
#left {
    width: 60px;
    overflow: hidden;
    height: 60px;
    background: linear-gradient(65deg, #2dff7747, #3bb360e8);
    position: absolute;
    cursor: pointer;
    right: 0;
    top: 14%;
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    transition: ease .3s;
}

#right:hover {
    transition: ease-in-out .5s;
    background: linear-gradient(65deg, #2dff77bf, #3bb36082);
}

.z-index {
    z-index: 7;
}

.arrow {
    font-size: 40px;
    color: white;
    padding-top: 7px;
    z-index: 7;
    padding-left: 5px;
}

#left {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border-top-right-radius: 50px !important;
    border-bottom-right-radius: 50px !important;
    left: 0 !important;
}

.opacity-left:hover,
.opacity-right:hover {
    opacity: 1;
    z-index: 6;
    transition: ease .5s;
    background: linear-gradient(65deg, #2dff778f, #5cb1e2e8);
}

.opacity-left {
    border-top-left-radius: 0 !important;
    border-bottom-left-radius: 0 !important;
    border-top-right-radius: 50px !important;
    border-bottom-right-radius: 50px !important;
    left: 0 !important;
}

.opacity-right,
.opacity-left {
    cursor: pointer;
    opacity: 0;
    z-index: 6;
    width: 60px;
    overflow: hidden;
    height: 60px;
    background: linear-gradient(65deg, #2dff7747, #3bb360e8);
    position: absolute;
    right: 0;
    top: 14%;
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    transition: ease .3s;
}

.arrow-left {
    font-size: 40px;
    z-index: 7;
    color: white;
    padding-top: 10px;
    padding-right: 5px;
}
